import React, { Component } from "react"
import { Link, navigate } from "gatsby"
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'
import Button from 'react-bootstrap/Button'
import config from "../../data/SiteConfig";
import logo from "./logo.jpg"

export default class MyNavbar extends Component {
  render() {
    return <>
<Navbar bg="light" expand="lg" fixed={this.props.fixed}>
  <Navbar.Brand onClick={()=>{navigate("/")}}>
  {config.siteTitle}
  </Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="mr-auto">
      <NavDropdown title="Lessons" id="basic-nav-dropdown">
        <NavDropdown.Item onClick={()=>{navigate("private-lessons")}}>Private Lessons</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item onClick={()=>{navigate("group-lessons")}}>Group Lessons</NavDropdown.Item>
      </NavDropdown>
      <Nav.Link onClick={()=>{navigate("corporate-classes")}}>Corporate Classes</Nav.Link>
      <NavDropdown title="Courses" id="basic-nav-dropdown">
        <NavDropdown.Item onClick={()=>{navigate("/special-course-test-prep#ielts対策コース")}}>IELTS Test Prep</NavDropdown.Item>
        <NavDropdown.Item onClick={()=>{navigate("/special-course-test-prep#toeic-and-toefl-コース")}}>TOEIC, TOEFL Test Prep</NavDropdown.Item>
        <NavDropdown.Item onClick={()=>{navigate("/special-course-test-prep#90-days-special-courses")}}>Special Courses</NavDropdown.Item>
      </NavDropdown>
    </Nav>
    <Nav>
    <Nav.Link onClick={()=>{navigate("/contact-us")}}>Contact Us</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>
    </>
  }
}
