import React from "react";
import Helmet from "react-helmet";
import config from "../../data/SiteConfig";
import "./index.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import MyNavbar from "../components/MyNavbar"
import Container from "react-bootstrap/Container"
import Jumbotron from "react-bootstrap/Jumbotron"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Nav from "react-bootstrap/Nav"
import Image from 'react-bootstrap/Image'
import { navigate } from 'gatsby'

export default class MainLayout extends React.Component {
  render() {
    const { children } = this.props;
    return <>
      <Helmet>
          <meta name="description" content={config.siteDescription} />
      </Helmet>
      <MyNavbar fixed="top"/>
　　　<Image src={this.props.image?
	this.props.image:
	["images/shield-1519642_1920.jpg"][Math.floor((Math.random() * 1))]} className="img-fluid w-100"/>
      <Container className="pt-5">
        {children}
      </Container>
<Jumbotron className="mb-0" fluid>
<Container>
<h2>{config.siteTitle}</h2>
<Row className="text-left">
<Col>
<Nav defaultActiveKey="#" className="flex-column">
  <Nav.Link onClick={()=>{navigate("private-lessons")}}>Private Lessons</Nav.Link>
  <Nav.Link onClick={()=>{navigate("group-lessons")}}>Group Lessons</Nav.Link>
  <Nav.Link onClick={()=>{navigate("corporate-classes")}}>Corporate Classes</Nav.Link>
</Nav>
</Col>
<Col>
<Nav defaultActiveKey="#" className="flex-column">
  <Nav.Link onClick={()=>{navigate("special-course-test-prep/#ielts対策コース")}}>Test Prep</Nav.Link>
  <Nav.Link onClick={()=>{navigate("special-course-test-prep/#90-days-special-courses")}}>Special Courses</Nav.Link>
</Nav>
</Col>
<Col>
<Nav defaultActiveKey="#" className="flex-column">
  <Nav.Link onClick={()=>{navigate("contact-us")}}>Contact Us</Nav.Link>
</Nav>
</Col>

</Row>
  </Container>
</Jumbotron>
<Row className="p-3">
<Col>
Copyright &copy; {(new Date()).getFullYear()} {config.siteTitle}
</Col>
</Row>

    </>
  }
}
