import React from "react";
import { Link, navigate } from "gatsby"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Card from "react-bootstrap/Card"
import Button from "react-bootstrap/Button"
import Badge from "react-bootstrap/Badge"

class PostCards extends React.Component {
  getPostList() {
    const postList = [];
    this.props.postEdges.forEach(postEdge => {
      if(this.props.categories.includes(postEdge.node.frontmatter.category))
      postList.push({
        path: postEdge.node.fields.slug,
        tags: postEdge.node.frontmatter.tags,
        category: postEdge.node.frontmatter.category,
        cover: postEdge.node.frontmatter.cover,
        title: postEdge.node.frontmatter.title,
        titleJa: postEdge.node.frontmatter.titleJa,
        date: postEdge.node.fields.date,
        excerpt: postEdge.node.excerpt,
        timeToRead: postEdge.node.timeToRead
      });
    });
    return postList;
  }
  render() {
    const postList = this.getPostList();
    return (
<Row>
{postList.map(post => (
<Col key={post.id} xs={12} sm={6} className="p-3">
<Card> 
  <Card.Img variant="top" src={post.cover} />
  <Card.Body>
    <Card.Title>{post.titleJa}<br/>{post.title}</Card.Title>
    <Card.Text>
     {post.excerpt}
    </Card.Text>
    <Link to={post.path} key={post.title}><Button variant="primary">Learn more</Button></Link>
  </Card.Body>
</Card>
</Col>
        ))}
</Row>
    );
  }
}

export default PostCards;
